export default {
	data() {
		return {
			password1:'',
			password2:'',
			password3:''
		}
	},
	methods: {
		reSetPassword() {
			if(this.password1.length==0 ||this.password2.length==0 || this.password3.length==0)
			{
				uni.showToast({
					title: '密码不能为空!',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			if(this.password2 != this.password3)
			{
				uni.showToast({
					title: '两次输入密码不一致!',
					duration: 2000,
					icon:'none'
				});
				return ;
			}
			this.bus.request({
				url: this.bus.url+'/user/changePwd',
				method: 'GET',
				 header:this.bus.user.header,
				data: {
					userId:this.bus.user.userId,
					oldPassword:this.password1,
					password:this.password2},
				success: res => {
					res = res.data;
					if(res.status=='1')
					{
						this.bus.login.password = this.password3
						uni.showToast({
							title: '密码修改成功!',
							duration: 2000
						});
					} else{
						uni.showToast({
							title: res.errMsg,
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					
				},
				complete: () => {
					uni.hideLoading()
				}
			});
		}
	}
}
